@import url(https://fonts.googleapis.com/css?family=Roboto);
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  height: 100%;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5em;

  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.Item {
  width: 420px;
  height: 260px;
  padding: 10px 10px 1px;
  margin: 20px 20px 10px 20px;

  background: #000000;
}

.Item h3 {
  display: table-cell;
  width: 430px;
  height: 210px;
  margin: 0 auto;

  font-weight: normal;
  font-size: 140%;
  text-align: center;
  vertical-align: middle;
}

.Item h3 a {
  color: #ffffff;
}

.Item iframe {
  display: block;
  width: 440px;
  height: 210px;
  margin: 0 auto;

  border: none;
}

.Item-views {
  margin: 6px 0 0 0;

  font-size: 80%;
  text-align: right;

  color: #cccccc;
}

.Highlight {
  position: relative;

  padding: 10px 10px 1px;
  margin: 10px;
}

.Highlight-label {
  position: absolute;
  top: 3px;
  left: 3px;

  display: inline-block;

  color: #ffffff;
  border: 1px solid;
  border-radius: 3px;
}

.Highlight .Item {
  width: 420px;
  margin: 0 auto;
}

.Highlight .Item iframe,
.Highlight .Item h3 {
  width: 400px;
}

.Highlight-new {
  background-color: rgba(41, 198, 31, 0.47);
}

.Highlight-new .Highlight-label {
  background-color: rgba(15, 145, 37, 0.72);
  border-color: rgba(15, 145, 37, 1);
}

.Highlight-popular {
  background-color: rgba(231, 0, 25, 0.64);
}

.Highlight-popular .Highlight-label {
  background-color: rgba(176, 0, 24, 0.73);
  border-color: rgba(176, 0, 24, 0, 1);
}

